import INetwork from "./INetwork";

export default class Network extends INetwork{

    send(options,url){
        if(typeof options === 'string'){
            options = {
                method:'get',
                url:options
            }
        } else if(url) {
            options.url = url
        }
        return this.instance(options)
    }

    async common(options,url){
        try{
            const response = await this.send(options,url);
            const isSuccess = this.isSuccess(response)
            const res = this.transformRes(response);
            if(isSuccess) {
                if(options.successNotice){
                    if(typeof options.success === "function") options.success(res,options)
                }
            }
        }catch (e) {
            console.error(e)
        }
        // è®¾ç½®äºè¿åæ°æ®çè¯ åè¿å dataæ°æ®
        if(options.haveData) return res;
        // å¦åè¿å boolean éç¨äºå é¤ä¹ç±»çæä½
        return isSuccess
    }
}